@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Lato",sans-serif}body{background:#cfc4f5 !important;height:100vh}.App{display:flex;align-items:center;justify-content:center;width:100%;height:calc(100vh - 5px);flex-wrap:wrap;background:#cfc4f5}.App #paused{display:none;position:absolute;background:rgba(51,51,51,0.78);height:calc(100vh + 5px);z-index:999;text-align:center;width:100%;font-weight:900;font-size:10vw;align-items:center;justify-content:center}.App #paused::before{content:'PAUSED';width:100%;display:block;background:#ff00c8;color:#cfc4f5}#flipCenter{width:400px;height:400px;display:grid;grid-template-columns:repeat(2, 1fr);position:absolute;visibility:visible;opacity:1;transition:opacity 0.25s linear}#flipCenter .letter{font-size:100px;display:flex;align-items:center;justify-content:center;font-weight:700;text-align:center;background:#ff641c;color:#f7f4eb;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;transition:0.6s;width:99%;height:99%;position:relative}#flipCenter .letter .back{position:absolute;-webkit-backface-visibility:hidden;backface-visibility:hidden;display:block;transform:rotateY(180deg);box-sizing:border-box;font-size:20px;width:100%;height:100%}#flipCenter .letter .back .level{font-size:25px;font-weight:700;display:block;transition:0.5s;margin:0;height:33.3333%;display:flex;flex-direction:column;justify-content:center;align-self:center}#flipCenter .letter .back .level:hover{background:#5866d0;color:#cfc4f5}#flipCenter .letter #iback{padding:6px}#flipCenter .letter #iback .ibacked{text-align:left;font-size:14px;margin:5px;font-weight:500}#flipCenter .letter #iback #pIns{font-size:18px;font-weight:700}#flipCenter .letter:hover{opacity:0.8}#flipCenter #l{background:#141a46;transform:scale(-1, 1);cursor:default}#flipCenter #l:hover{opacity:1}#flipCenter #fAnim,#flipCenter #iAnim,#flipCenter #pAnimation{transform:rotateY(-180deg);background:#141a46;width:calc(200% - 2px);z-index:2;transform-style:preserve-3d;transition:all 0.4s linear}#flipCenter #fAnim .front,#flipCenter #iAnim .front,#flipCenter #pAnimation .front{opacity:0;background:#ff641c}#flipCenter #pAnimation{width:99%;z-index:1}#flipCenter #lAnim,#flipCenter #pAnim{opacity:0;transition:0.4s;background:#ff641c}#flipCenter #pAnim{transform:scale(-1, 1);background:#141a46}#flipCenter #fAnim:hover,#flipCenter #iAnim:hover,#flipCenter #pAnimation:hover{opacity:1}#game{display:none}#gameActive{width:100%;height:100%}#gameActive .progress{border-radius:0;background:#cfc4f5;height:5px}#gameActive .progress .progress-bar{background:#ff00c8;transition:width 0.5s linear}#gameActive .gameInside{display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(4, 1fr);width:100%;height:100%;box-sizing:border-box;-webkit-animation:fadein 0.3s;animation:fadein 0.3s;z-index:998;grid-gap:4px;grid-auto-rows:minmax(50px, auto)}#gameActive .gameInside .indent{background:#ff641c;cursor:pointer;padding:5px;position:relative;box-sizing:border-box;transition:all 0.25s linear}#gameActive .gameInside .indent .backCard{position:absolute;-webkit-backface-visibility:hidden;backface-visibility:hidden;display:block;transform:rotateY(180deg);box-sizing:border-box;font-size:20px}#gameActive .gameInside .indentAnim{box-sizing:border-box;display:flex;justify-content:center;align-items:center;transform:rotateY(-180deg);background:#141a46;z-index:2;transform-style:preserve-3d;transition:all 0.25s linear;cursor:pointer}#gameActive .gameInside .indentAnim .frontCard{opacity:0;background:#ff641c}#gameActive .gameInside .indentAnim .backCard{display:flex;justify-content:center;align-items:center;padding:5px}#gameActive .gameInside .indentAnim .backCard svg{transform:rotateY(-180deg);width:30%;height:30%;color:#f7f4eb}#flipCenter.hide-opacity{opacity:0}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}@keyframes fadein{from{opacity:0}to{opacity:1}}

